body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.post-form-container {
  width: 50%;
  margin: 0 auto;
}

.post-form-container h2 {
  margin-bottom: 20px;
}

.form-group {
  margin-bottom: 15px;
}

.form-group label {
  display: block;
  margin-bottom: 5px;
}

.form-group input[type="text"],
.form-group textarea {
  width: 100%;
  padding: 8px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.form-group textarea {
  height: 150px;
}

button {
  padding: 10px 20px;
  font-size: 16px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}
.post-delete-container {
  width: 50%;
  margin: 0 auto;
}

.post-delete-container p {
  margin-bottom: 20px;
}

.post-delete-container button {
  padding: 10px 20px;
  font-size: 16px;
  background-color: #dc3545;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.post-delete-container button:hover {
  background-color: #c82333;
}
.post-list-container {
  width: 50%;
  margin: 0 auto;
}

.post-list-container h1 {
  margin-bottom: 20px;
}

.post-list-container ul {
  list-style-type: none;
  padding: 0;
}

.post-list-container li {
  margin-bottom: 10px;
}

.post-list-container li a {
  display: block;
  padding: 10px;
  background-color: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 4px;
  text-decoration: none;
  color: #333;
}

.post-list-container li a:hover {
  background-color: #ddd;
}

.post-detail-container {
  width: 50%;
  margin: 0 auto;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 4px;
  background-color: #f9f9f9;
}

.post-detail-container h2 {
  margin-bottom: 20px;
}

.post-detail-container h3 {
  margin-bottom: 10px;
}

.post-detail-container p {
  margin-bottom: 20px;
}

.post-detail-container .edit-link,
.post-detail-container .delete-link,
.post-detail-container .back-link {
  display: block;
  margin-bottom: 10px;
  text-decoration: none;
  color: #007bff;
}

.post-detail-container .edit-link:hover,
.post-detail-container .delete-link:hover,
.post-detail-container .back-link:hover {
  text-decoration: underline;
}
